#portfolio {
  min-width: 350px;
  width: 80%;
  max-width: 780px;
  border: 1px solid white;
  padding: 20px;
  margin-top: 75px;
  margin-bottom: 75px;
  float: center;
  margin-left: auto;
}

.img-portfolio {
  display: flex;
  max-width: auto;
  height: auto;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 20px;
  float: left;
}
h2 {
  border-bottom-style: solid;
  border-color: black;
  border-width: 1px;
  padding-bottom: 10px;
  margin-top: 70px;
  font-family: "Homemade Apple", cursive;
}
h2.h2 {
  color: #d2fdff;
  font-family: serif;
  font-weight: bold;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  font-size: 70px;
  font-family: "Homemade Apple", cursive;
}
